import React, {Component} from 'react';
import Head from 'next/head';

import SearchPrimary from '../components/SearchPrimary';
import PrimaryButton from '../components/PrimaryButton';

import withRedux from '../lib/withRedux';

import { updateSearchTerm, toggleSavedSearch } from '../actions/search';


class Dashboard extends Component {
  onSubmit(e) {
    e.preventDefault();
    this.props.dispatch(updateSearchTerm(this.state.name));
    this.props.dispatch(toggleSavedSearch(false));
    Router.push("/search-results");
  }
  
  render() {
    return(
      <div>
        <Head>
          <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/normalize/7.0.0/normalize.css" />
          <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        </Head>

        <h3>(DASHBOARD PAGE)</h3>
        <SearchPrimary />

        <div className="primary-search-button">
          <PrimaryButton handleOnSubmit={this.props.onSubmit}/>
        </div>


        <style jsx>{`
          div {
            background: lightgrey;
            padding: 24px;
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            height: 100vw;
          }
          .primary-search-button {
            display: none;
          }
          @media all and (min-width: 376px) {
            .primary-search-button {
              display: flex
            }
          }
        `}
        </style>
      </div>
    );
  }
}

export default withRedux(Dashboard);


